/* Basic */

html {
	box-sizing: border-box;
}

*,
*:before,
*:after {
	box-sizing: inherit;
}

body {
	background: #293136;
}

body.is-preload *,
body.is-preload *:before,
body.is-preload *:after {
	-moz-animation: none !important;
	-webkit-animation: none !important;
	-ms-animation: none !important;
	animation: none !important;
	-moz-transition: none !important;
	-webkit-transition: none !important;
	-ms-transition: none !important;
	transition: none !important;
}

body,
input,
textarea,
select {
	font-family: "PT Sans", sans-serif;
	font-size: 10pt;
	line-height: 2em;
	color: #7a8186;
}

h1,
h2,
h3,
h4,
h5,
h6 {
	line-height: 1em;
	font-weight: bold;
	color: #3a444a;
}

h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a {
	color: inherit;
	text-decoration: none;
}

strong,
b {
	font-weight: bold;
	color: #3a444a;
}

em,
i {
	font-style: italic;
}

a {
	color: #61a8df;
}

sub {
	position: relative;
	top: 0.5em;
	font-size: 0.8em;
}

sup {
	position: relative;
	top: -0.5em;
	font-size: 0.8em;
}

hr {
	border: 0;
	border-top: solid 1px #ddd;
}

blockquote {
	border-left: solid 0.5em #ddd;
	padding: 1em 0 1em 2em;
	font-style: italic;
}

p,
ul,
ol,
dl,
table {
	margin-bottom: 1em;
}

header {
	margin-bottom: 1em;
}

header h2 {
	display: block;
	margin: 0em;
	padding: 0em;
	letter-spacing: 0.05em;
	font-size: 1.6em;
	font-weight: 700;
}

header .byline {
	display: block;
	margin: 0.5em 0 0 0;
	padding: 0 0 0.5em 0;
	font-weight: 400;
}

footer {
	margin-top: 1em;
}

br.clear {
	clear: both;
}

