}

/* Lists */

ul {
	list-style: disc;
	padding-left: 1.25em;
}

ul li {
	padding-left: 0.25em;
}

ul.alt {
	margin: 0;
	padding: 1em 0em 0em 0em;
}

ul.alt li {
	display: block;
	padding: 0.6em 0em;
	border-top: 1px solid #dcdcdc;
}

