	display: inline-block;
	line-height: 40px;
	font-size: 1.2em;
	color: #fff;
}

ul.icons li a:hover {
	background: rgba(255, 255, 255, 0.05);
	border-color: #6b7982;
}

/* Info */

ul.info {
	list-style: none;
	padding-left: 0;
}

ul.info li {
	padding: 2em 0 1.5em 0;
	border-top: 1px solid #dcdcdc;
